<script>

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/scrollbar";

// import required modules
import { Scrollbar } from "swiper";
import useValidate from '@vuelidate/core'
import { required, helpers, minLength, maxLength, email, numeric} from '@vuelidate/validators'
import { reactive, computed } from 'vue'
import PreloaderVac from './PreloaderVac.vue'

export default {

    setup(){
        const state = reactive({
            monthly: '',
            employer: '',
            title: '',
            email: '',
            year: '',
            mounth: '',
            address: '',
            city: '',
            province: '',
            postCode: '',
            year2: '',
            mounth2: '',
            day: '',
            firstName: '',
            lastName: '',
            phone: '',
        })

        const rules = computed(() => {
            return{
                monthly: { required, numeric },
                year: { required, numeric, minLength: minLength(4), maxLength: maxLength(4) },
                year2: { required, numeric, minLength: minLength(4), maxLength: maxLength(4) },
                mounth: { required, numeric, minLength: minLength(1), maxLength: maxLength(2) },
                mounth2: { required, numeric, minLength: minLength(1), maxLength: maxLength(2) },
                day: { required, numeric, minLength: minLength(1), maxLength: maxLength(2) },
                employer: { required },
                title: { required },
                address: { required },
                province: { required },
                city: { required },
                postCode: { required, numeric },
                phone: { required, minLength: minLength(10), numeric },
                email: { required, email }, 
                firstName: { required },
                lastName: { required },

            }
        })

        const v$ = useValidate(rules, state)

        return{
            state,
            v$,
            modules: [Scrollbar],
        }
    },

    data(){
        return{
            questionIndex: 0,
            activeBnt: false,
            employedBtn: false,
            studentBtn: false,
            retiredBtn: false,
            otherBtn: false,
            inputRandom: '',
        }
    },

    methods:{
        next(){
            let progress = document.querySelector('.quiz-progress')
            let resProgress = 10
            this.inputRandomNumber()
           // this.progressBar()
            if(this.activeBnt === true){
                this.questionIndex++
                this.activeBnt = false
            } else if(this.questionIndex === 2 && this.employedBtn === true){
                this.v$.monthly.$validate()
                if(this.v$.monthly.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 3 && this.employedBtn === true){
                this.v$.employer.$validate()
                this.v$.title.$validate()
                if(this.v$.employer.$error === false && this.v$.title.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 4 && this.employedBtn === true){
                this.v$.year.$validate()
                this.v$.mounth.$validate()
                if(this.v$.year.$error === false && this.v$.mounth.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 5 && this.employedBtn === true){
                this.v$.address.$validate()
                this.v$.city.$validate()
                this.v$.province.$validate()
                this.v$.postCode.$validate()
                if(this.v$.address.$error === false && this.v$.city.$error === false && this.v$.province.$error === false && this.v$.postCode.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 6 && this.employedBtn === true){
                this.v$.year2.$validate()
                this.v$.mounth2.$validate()
                this.v$.day.$validate()
                if(this.v$.year2.$error === false && this.v$.mounth2.$error === false && this.v$.day.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 7 && this.employedBtn === true){
                this.v$.firstName.$validate()
                this.v$.lastName.$validate()
                this.v$.email.$validate()
                this.v$.phone.$validate()
                if(this.v$.firstName.$error === false && this.v$.lastName.$error === false && this.v$.email.$error === false && this.v$.phone.$error === false){
                    this.questionIndex++
                }
            }  else if(this.questionIndex === 2 && this.studentBtn === true){
                this.v$.monthly.$validate()
                if(this.v$.monthly.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 3 && this.studentBtn === true){
                this.v$.year.$validate()
                this.v$.mounth.$validate()
                if(this.v$.year.$error === false && this.v$.mounth.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 4 && this.studentBtn === true){
                this.v$.address.$validate()
                this.v$.city.$validate()
                this.v$.province.$validate()
                this.v$.postCode.$validate()
                if(this.v$.address.$error === false && this.v$.city.$error === false && this.v$.province.$error === false && this.v$.postCode.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 5 && this.studentBtn === true){
                this.v$.year2.$validate()
                this.v$.mounth2.$validate()
                this.v$.day.$validate()
                if(this.v$.year2.$error === false && this.v$.mounth2.$error === false && this.v$.day.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 6 && this.studentBtn === true){
                this.v$.firstName.$validate()
                this.v$.lastName.$validate()
                this.v$.email.$validate()
                this.v$.phone.$validate()
                if(this.v$.firstName.$error === false && this.v$.lastName.$error === false && this.v$.email.$error === false && this.v$.phone.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 2 && this.retiredBtn === true){
                this.v$.monthly.$validate()
                if(this.v$.monthly.$error === false){
                    this.questionIndex++
                }
            } else if(this.activeBnt === true && this.questionIndex === 3 && this.retiredBtn === true){
                this.questionIndex++
                this.activeBnt = false
            } else if(this.questionIndex === 4 && this.retiredBtn === true){
                this.v$.address.$validate()
                this.v$.city.$validate()
                this.v$.province.$validate()
                this.v$.postCode.$validate()
                if(this.v$.address.$error === false && this.v$.city.$error === false && this.v$.province.$error === false && this.v$.postCode.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 5 && this.retiredBtn === true){
                this.v$.year2.$validate()
                this.v$.mounth2.$validate()
                this.v$.day.$validate()
                if(this.v$.year2.$error === false && this.v$.mounth2.$error === false && this.v$.day.$error === false){
                    this.questionIndex++
                }
            } else if(this.v$.firstName.$error === false && this.v$.lastName.$error === false && this.v$.email.$error === false && this.v$.phone.$error === false && this.questionIndex === 6 && this.retiredBtn === true){
                this.v$.firstName.$validate()
                this.v$.lastName.$validate()
                this.v$.email.$validate()
                this.v$.phone.$validate()
                if(this.v$.firstName.$error === false && this.v$.lastName.$error === false && this.v$.email.$error === false && this.v$.phone.$error === false){
                    this.questionIndex++
                }
            }  else if(this.activeBnt === true && this.questionIndex === 2 && this.otherBtn === true){
                this.questionIndex++
                this.activeBnt = false
            } else if(this.v$.monthly.$error === false && this.questionIndex === 3 && this.otherBtn === true){
                this.v$.monthly.$validate()
                if(this.v$.monthly.$error === false){
                    this.questionIndex++
                }
            } else if(this.activeBnt === true && this.questionIndex === 4 && this.otherBtn === true){
                this.questionIndex++
                this.activeBnt = false
            } else if(this.questionIndex === 5 && this.otherBtn === true){
                this.v$.address.$validate()
                this.v$.city.$validate()
                this.v$.province.$validate()
                this.v$.postCode.$validate()
                if(this.v$.address.$error === false && this.v$.city.$error === false && this.v$.province.$error === false && this.v$.postCode.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 6 && this.otherBtn === true){
                this.v$.year2.$validate()
                this.v$.mounth2.$validate()
                this.v$.day.$validate()
                if(this.v$.year2.$error === false && this.v$.mounth2.$error === false && this.v$.day.$error === false){
                    this.questionIndex++
                }
            } else if(this.questionIndex === 7 && this.otherBtn === true){
                this.v$.firstName.$validate()
                this.v$.lastName.$validate()
                this.v$.email.$validate()
                this.v$.phone.$validate()
                if(this.v$.firstName.$error === false && this.v$.lastName.$error === false && this.v$.email.$error === false && this.v$.phone.$error === false){
                    this.questionIndex++
                }
            }

            if(this.questionIndex === 1){
                resProgress = 15
            } else if(this.questionIndex === 2){
                resProgress = 25
            } else if(this.questionIndex === 3){
                resProgress = 42
            } else if(this.questionIndex === 4){
                resProgress = 58
            } else if(this.questionIndex === 5){
                resProgress = 74
            } else if(this.questionIndex === 6){
                resProgress = 90
            } else if(this.questionIndex === 7){
                resProgress = 100
            }
            progress.style.width = resProgress + '%'

        },
        prev(){
            this.questionIndex--
            this.activeBnt = false
            //this.progressBar()
            if(this.questionIndex === 1){
                this.employedBtn = false
                this.studentBtn = false
                this.retiredBtn = false
                this.otherBtn = false
            }

            let progress = document.querySelector('.quiz-progress')
            let resProgress = 10

            if(this.questionIndex === 1){
                resProgress = 15
            } else if(this.questionIndex === 2){
                resProgress = 25
            } else if(this.questionIndex === 3){
                resProgress = 42
            } else if(this.questionIndex === 4){
                resProgress = 58
            } else if(this.questionIndex === 5){
                resProgress = 74
            } else if(this.questionIndex === 6){
                resProgress = 90
            } else if(this.questionIndex === 7){
                resProgress = 100
            }
            progress.style.width = resProgress + '%'
        },
        employedClick(){
            this.activeBnt = true 
            this.employedBtn = true 
        },
        studentClick(){
            this.activeBnt = true 
            this.studentBtn = true
        },
        retiredClick(){
            this.activeBnt = true 
            this.retiredBtn = true
        },
        otherClick(){
            this.activeBnt = true 
            this.otherBtn = true
        },
        inputRandomNumber(){
                let words = '0123456789qwertyuiopasdfghjklzxcvbnmQWERTYUIOPASDFGHJKLZXCVBNM',
                number = '0123456789',
                result = '',
                result2 = '';
                for(let i = 0; i < 7; i++){
                    result += number[Math.round(Math.random() * (number.length - 1))];
                }
                for(let i = 0; i < 5; i++){
                    result2 += words[Math.round(Math.random() * (words.length - 1))];
                }
                return this.inputRandom = result + '-' + result2
        },
    },    

    computed: {
        inventoryCars(){
            return this.$store.getters.inventoryCars
        },
    },    

    components: {
        Swiper,
        SwiperSlide,
        PreloaderVac,
    },
    // setup() {
    //     return {
    //     };
    // },
    
}
</script>
